import { ExtendedFirestoreInstance } from 'react-redux-firebase';

import { COLLECTION_NAME_MAP, Transliterator } from './';

type TransliteratorDocumentData = Omit<Transliterator, 'id'>;
export const getAllTransliterators = async (
  firestore: ExtendedFirestoreInstance
): Promise<Transliterator[]> => {
  const transliteratorSnapRef = await firestore
    .collection(COLLECTION_NAME_MAP.TRANSLITERATOR)
    .get();
  return transliteratorSnapRef.docs.map((doc) => ({
    id: Number(doc.id),
    ...(doc.data() as TransliteratorDocumentData),
  }));
};
