import React from 'react';

import { makeStyles, Chip } from '@material-ui/core';
import clsx from 'clsx';
import { useRoute, useNavigation } from 'src/lib/hooks';

const useStyles = makeStyles((theme) => ({
  routeContainer: {
    display: 'flex',
  },
  root: {
    display: 'flex',
    flexDirection: 'row',
    justifyContent: 'start',
    alignItems: 'center',
    flexWrap: 'wrap',
  },
  chipRoot: {
    padding: '0px 0.6em',
    fontSize: '0.6em',
    height: '1.3em',
    margin: `0 ${theme.spacing(1 / 2)}px ${theme.spacing(1 / 2)}px 0`,
    [theme.breakpoints.up('sm')]: {
      maxWidth: 200,
    },
  },
}));

export type KuralChipItem = {
  label: string;
  navRoute: string;
  color: 'default' | 'primary' | 'secondary';
};
type RouteViewerProps = {
  className?: string;
  renderRight?: React.ReactNode;
};

const RouteViewer: React.FC<RouteViewerProps> = (props: RouteViewerProps) => {
  const classes = useStyles(props);
  const [navigateTo] = useNavigation();
  const [, routes] = useRoute({});
  return (
    <div className={clsx(classes.routeContainer, props.className)}>
      <div className={classes.root}>
        {routes.map((item, index) => (
          <Chip
            size='small'
            label={item.value}
            color={'primary'}
            onClick={(): void => {
              if (item.route) {
                navigateTo(item.route)();
              }
            }}
            style={{ backgroundColor: item.color }}
            classes={{ root: classes.chipRoot }}
            key={index}
          />
        ))}
      </div>
      {props.renderRight}
    </div>
  );
};

export default RouteViewer;
