/* eslint-disable no-fallthrough */
import { ExtendedFirestoreInstance } from 'react-redux-firebase';

import { COLLECTION_NAME_MAP, BaseType, Chapter } from './';
import {
  IndexMapType,
  Literature,
  SearchCategoryTypes,
  SearchResult,
  Section,
} from './types';

export const getKuralIndex = async (
  firestore: ExtendedFirestoreInstance
): Promise<IndexMapType> => {
  const indexSnapshot = await firestore
    .collection(COLLECTION_NAME_MAP.INDEX_MAP)
    .get();
  const getDocData = (key: 'section' | 'litChapMap'): any =>
    indexSnapshot.docs.find((doc) => doc.id === key);
  return {
    section: getDocData('section').data(),
    litChapMap: getDocData('litChapMap').data(),
  };
};

export const getAllBaseItems = async (
  firestore: ExtendedFirestoreInstance,
  lang: string,
  collectionKey: keyof typeof COLLECTION_NAME_MAP
): Promise<BaseType[]> => {
  const sectionSnapshot = await firestore
    .collection(COLLECTION_NAME_MAP[collectionKey])
    .get();
  return sectionSnapshot.docs.map((doc) => ({
    id: Number(doc.id),
    value: doc.data()[lang] as string,
  }));
};

export const getAllSections = async (
  firestore: ExtendedFirestoreInstance,
  lang: string
): Promise<Section[]> => await getAllBaseItems(firestore, lang, 'SECTION');

export const getAllChapter = async (
  firestore: ExtendedFirestoreInstance,
  lang: string
): Promise<Chapter[]> => await getAllBaseItems(firestore, lang, 'CHAPTER');

export const getAllLiterature = async (
  firestore: ExtendedFirestoreInstance,
  lang: string
): Promise<Literature[]> => {
  return getAllBaseItems(firestore, lang, 'LITERATURE');
};

export const findChapterNoFromKuralNo = (kuralNo: number): number =>
  Math.ceil(kuralNo / 10);

export const isResultOfType = (
  result: SearchResult,
  type: SearchCategoryTypes
): boolean => {
  const resType = result.type;
  if (type === 'chapter') {
    return resType.startsWith('ByChapter');
  }
  if (type === 'kural') {
    return resType.startsWith('ByKural');
  }
  if (type === 'detail') {
    return resType.startsWith('ByKuralDetail');
  }
  if (type === 'section') {
    return resType.startsWith('BySection');
  }
  if (type === 'literature') {
    return resType.startsWith('ByLiterature');
  }
  return false;
};
