import firebase from 'firebase/app';
import { ExtendedFirestoreInstance } from 'react-redux-firebase';

import {
  isCategoryValidErr,
  COLLECTION_NAME_MAP,
  KuralDetail,
  Kural,
} from './';

export const getKural = async (
  firestore: ExtendedFirestoreInstance,
  kId: number,
  lang: string
): Promise<Kural> => {
  isCategoryValidErr('kural', kId);
  const kuralSnapshotRef = await firestore
    .collection(COLLECTION_NAME_MAP.KURAL)
    .doc(`${kId}`)
    .get();
  // eslint-disable-next-line @typescript-eslint/no-non-null-assertion
  return { id: kId, value: kuralSnapshotRef.data()!![lang] };
};

export const getKuralDetail = async (
  firestore: ExtendedFirestoreInstance,
  kId: number
): Promise<KuralDetail> => {
  isCategoryValidErr('kural', kId);
  const snapshot = await firestore
    .collection(COLLECTION_NAME_MAP.KURAL_DETAIL)
    .doc(`${kId}`)
    .get();
  return snapshot.data() as Record<string, string>;
};

export const getKuralsOfChapter = async (
  firestore: ExtendedFirestoreInstance,
  chapter: number,
  lang: string
): Promise<Kural[]> => {
  isCategoryValidErr('chapter', chapter);
  const startKuralNo = (chapter - 1) * 10 + 1;
  const kuralRangeIds = Array.from(Array(10).keys()).map(
    (i) => `${startKuralNo + i}`
  );
  const kuralsRef = firestore.collection(COLLECTION_NAME_MAP.KURAL);
  const snapshot = await kuralsRef
    .where(firebase.firestore.FieldPath.documentId(), 'in', kuralRangeIds)
    .get();
  const result = snapshot.docs.map((doc) => ({
    id: Number(doc.id),
    value: doc.data()[lang],
  }));
  return result.sort((a, b) => a.id - b.id);
};
