import { ExtendedFirestoreInstance } from 'react-redux-firebase';
import { ThunkAction } from 'redux-thunk';
import {
  getAllChapter,
  getAllLiterature,
  getAllSections,
  getAllTransliterators,
  getKuralIndex,
  getUserPreferences,
  toBaseTypeMap,
  Transliterator,
} from 'src/lib/api';

import { AppState } from '../reducers';
import { UpdateSettingsAction } from './../settings';
import { applyPreference } from './../settings';
import { InitIndexMapAction } from './types';

export const thunkInitKuralIndexMapAndUserPref = (
  firestore: ExtendedFirestoreInstance
): ThunkAction<
  void,
  AppState,
  unknown,
  InitIndexMapAction | UpdateSettingsAction
> => {
  return async (dispatch): Promise<void> => {
    // TODO: fetch data only in userPrimary Language
    const userPref = await getUserPreferences(firestore, 'rajeshsundaram');
    const kuralIndex = await getKuralIndex(firestore);

    const sectionMap = toBaseTypeMap(
      await getAllSections(firestore, userPref.primaryLanguage)
    );
    const literatureMap = toBaseTypeMap(
      await getAllLiterature(firestore, userPref.primaryLanguage)
    );
    const chapterMap = toBaseTypeMap(
      await getAllChapter(firestore, userPref.primaryLanguage)
    );
    const transliteratorMap = (await getAllTransliterators(firestore)).reduce(
      (oldVal, t1) => ({ ...oldVal, [t1.id]: t1 }),
      {}
    ) as Record<number, Transliterator>;
    dispatch(applyPreference(userPref));
    dispatch({
      type: 'init',
      payload: {
        section: sectionMap,
        chapter: chapterMap,
        literature: literatureMap,
        index: kuralIndex,
        transliterator: transliteratorMap,
      },
    });
  };
};
