import { UserPreference } from 'src/lib/api';

type LoadSettingsAction = { type: 'LoadSettings'; payload: UserPreference };
export type UpdateSettingsAction = {
  type: 'UpdateSettings';
  payload: Partial<UserPreference>;
};
type SettingsActionTypes = LoadSettingsAction | UpdateSettingsAction;
export const SETTINGS_STORAGE = 'settings';

export const applyPreference = (
  pref: Partial<UserPreference>
): UpdateSettingsAction => {
  return {
    type: 'UpdateSettings',
    payload: pref,
  };
};

export const defaultSettings: UserPreference = {
  fontSize: 16,
  primaryLanguage: 'TA',
};
export const SettingsReducer = (
  settings = defaultSettings,
  action: SettingsActionTypes
): UserPreference => {
  switch (action.type) {
    case 'LoadSettings':
      return action.payload;
    case 'UpdateSettings':
      return { ...settings, ...action.payload };
    default:
      return settings;
  }
};
