import React from 'react';

import {
  makeStyles,
  List,
  ListItem,
  ListItemText,
  InputLabel,
  Select,
  FormControl,
  MenuItem,
} from '@material-ui/core';
import { useSelector, useDispatch } from 'react-redux';

import { AppState } from '../../../state/reducers';
import {
  applyPreference,
  defaultSettings,
  SETTINGS_STORAGE,
} from '../../../state/settings';

const useStyles = makeStyles((theme) => ({
  root: {},
  item: {},
  itemText: {
    fontSize: '1em',
  },
  formControl: {
    margin: theme.spacing(1),
    minWidth: 120,
  },
}));

type SettingsProps = {
  className?: string;
};

const Settings: React.FC<SettingsProps> = (props: SettingsProps) => {
  const classes = useStyles();
  const dispatch = useDispatch();
  const settings = useSelector((st: AppState) => st.settings);

  const fontSizeOptions: Array<number> = [];
  Array.from(Array(10).keys()).reduce((counter) => {
    fontSizeOptions.push(counter);
    counter = counter + 2;
    return counter;
  }, defaultSettings.fontSize - 4);
  const onFontChange = (value: number): void => {
    dispatch(applyPreference({ fontSize: value }));
  };

  // TODO:
  // React.useEffect(() => {
  //   (async (): Promise<void> => {
  //     await localforage.setItem(SETTINGS_STORAGE, settings);
  //   })();
  // }, [settings]);

  return (
    <div className={props.className}>
      <List>
        <ListItem className={classes.item}>
          <ListItemText
            classes={{ primary: classes.itemText }}
            primary='font size'
          />
          <FormControl className={classes.formControl}>
            <InputLabel id='demo-simple-select-label'>Age</InputLabel>
            <Select
              labelId='demo-simple-select-label'
              id='demo-simple-select'
              value={settings.fontSize}
              onChange={(e): void => onFontChange(e.target.value as number)}>
              {fontSizeOptions.map((v) => (
                <MenuItem key={v} value={v}>
                  {v}
                </MenuItem>
              ))}
            </Select>
          </FormControl>
        </ListItem>
      </List>
    </div>
  );
};

export default Settings;
