import React from 'react';

import { makeStyles, Badge } from '@material-ui/core';
import ListItem from '@material-ui/core/ListItem';
import clsx from 'clsx';
import { Kural } from 'src/lib/api';

const useStyles = makeStyles((theme) => ({
  listItem: {
    [theme.breakpoints.up('sm')]: {
      cursor: 'pointer',
    },
  },
  kuralListItem: {
    flexDirection: 'row',
    alignItems: 'start',
    justifyContent: 'space-between',
  },
  badgeContainer: {
    position: 'absolute',
  },
  kuralBadge: {
    opacity: 0.8,
    height: '1.5em',
    fontSize: '0.5em',
    top: theme.spacing(-1),
    left: 2,
  },
  kuralWrapper: {
    width: '100%',
  },
}));

type KuralCardProps = {
  kural: Kural;
  onClick?: (kural: Kural) => void;
  className?: string;
  showBadgeRight?: boolean;
  /**
   * The component used for the root node.
   * e.g) a, li
   */
  componentType?: any;
};
const KuralCard: React.FC<KuralCardProps> = (props: KuralCardProps) => {
  const classes = useStyles();
  return (
    <ListItem
      className={props.className}
      classes={{ root: clsx(classes.listItem, classes.kuralListItem) }}
      component={props.componentType || 'li'}>
      <Badge
        color='primary'
        badgeContent={props.kural.id}
        anchorOrigin={{ vertical: 'top', horizontal: 'left' }}
        className='kural-badge'
        classes={{
          badge: classes.kuralBadge,
          root: classes.badgeContainer,
        }}
        max={1330}
      />
      <div
        onClick={(): void => props.onClick && props.onClick(props.kural)}
        className={classes.kuralWrapper}
        dangerouslySetInnerHTML={{
          __html: props.kural.value.replace('\n', '<br>'),
        }}></div>
    </ListItem>
  );
};

export default KuralCard;
