import React from 'react';

import { renderRoutes, RouteConfigComponentProps } from 'react-router-config';
import { useKuralHeader } from 'src/lib/hooks';

import MyAppBar from '../../components/common/MyAppBar';
import KuralActionBar from '../../components/kural/KuralActionBar';

const Main: React.FC<RouteConfigComponentProps> = (
  props: RouteConfigComponentProps
) => {
  const [title, actions] = useKuralHeader();
  return (
    <>
      <MyAppBar title={title} actions={actions} />
      {renderRoutes(props.route?.routes)}
      <KuralActionBar />
    </>
  );
};

export default Main;
