import React from 'react';

import Divider from '@material-ui/core/Divider';
import IconButton from '@material-ui/core/IconButton';
import InputBase from '@material-ui/core/InputBase';
import Paper from '@material-ui/core/Paper';
import { makeStyles, Theme, createStyles } from '@material-ui/core/styles';
import ClearIcon from '@material-ui/icons/Clear';
import SearchIcon from '@material-ui/icons/Search';
import { useSelector } from 'react-redux';
import { AppState } from 'src/state/reducers';
import { useDebouncedCallback } from 'use-debounce';

import { useKuralSearch } from '../../../lib/hooks';

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    root: {
      padding: '2px 4px',
      display: 'flex',
      alignItems: 'center',
      maxWidth: 400,
    },
    input: {
      marginLeft: theme.spacing(1),
      flex: 1,
    },
    iconButton: {
      padding: 10,
    },
    divider: {
      height: 28,
      margin: 4,
    },
  })
);

type KuralSearchProps = {
  className?: string;
};

/* const SearchOptions = [
  {
    id: 1,
    label: 'All', // kural_no, startsWith, endsWith, contains
  },
];

const ITEM_HEIGHT = 48;
const ITEM_PADDING_TOP = 8;
const MenuProps = {
  PaperProps: {
    style: {
      maxHeight: ITEM_HEIGHT * 4.5 + ITEM_PADDING_TOP,
      width: 250,
    },
  },
};
 */
const KuralSearch: React.FC<KuralSearchProps> = (props: KuralSearchProps) => {
  const classes = useStyles();
  const search = useSelector((st: AppState) => st.search);
  const [setKeyword, clearSearchResults] = useKuralSearch();
  const [debouncedCallback] = useDebouncedCallback((value: string) => {
    setKeyword(value);
  }, 500);
  const [searchKeyword, setSearchKeyword] = React.useState<string>(
    search.keyword
  );
  //   const onSearchOptionChange = console.log;
  return (
    <Paper component='form' className={classes.root}>
      {/* <Select
        labelId='demo-mutiple-name-label'
        id='demo-mutiple-name'
        multiple
        value={[SearchOptions[0].id]}
        onChange={onSearchOptionChange}
        input={<Input />}
        MenuProps={MenuProps}>
        {SearchOptions.map((item) => (
          <MenuItem key={item.id} value={item.label}>
            {item.label}
          </MenuItem>
        ))}
      </Select> */}
      <IconButton
        disabled
        type='submit'
        color='primary'
        className={classes.iconButton}
        aria-label='search'>
        <SearchIcon />
      </IconButton>
      <InputBase
        className={classes.input}
        // TODO: placeholder: 12, kural, chapter_name
        placeholder='Search kurals'
        inputProps={{ 'aria-label': 'Search' }}
        onChange={(e): void => {
          setSearchKeyword(e.target.value);
          debouncedCallback(e.target.value);
        }}
        value={searchKeyword}
      />
      <Divider className={classes.divider} orientation='vertical' />
      <IconButton
        disabled={!search.keyword.length}
        type='submit'
        color='primary'
        className={classes.iconButton}
        onClick={(): void => {
          setSearchKeyword('');
          clearSearchResults();
        }}
        aria-label='search'>
        <ClearIcon />
      </IconButton>
    </Paper>
  );
};

export default KuralSearch;
