export const useKuralSearch: UseKuralSearchType = () => {
  const search = useSelector((st: AppState) => st.search);
  const dispatch = useDispatch();
  const setSearchKey = (keyword: string): void => {
    dispatch(setSearchKeyword(keyword));
  };
  const clearSearch = (): void => {
    dispatch(clearSearchResults());
    dispatch(setSearchKeyword(''));
    dispatch(setSearchFilters([]));
  };
  React.useEffect(() => {
    // TODO: redux-thunk
    const searchTypes = findSuitableSearchTypes(search.keyword);
    dispatch(setSearchFilters(searchTypes));
    dispatch(clearSearchResults());
    addResults(searchTypes, search.keyword, dispatch);
  }, [dispatch, search.keyword]);
  return [setSearchKey, clearSearch];
};
